import {RxClockFace, RxClockFaceTick, RxClockFaceTicks} from '../adapt-rx-datetime.types';

const HOURS_24_TICKS: RxClockFaceTicks = [
  Array(12)
    .fill(0)
    .map((x, i) => {
      const tick: RxClockFaceTick = {
        value: i || 12,
        uiValue: (i || 12).toString(),
        isDisabled: false
      };

      return tick;
    }),
  Array(12)
    .fill(0)
    .map((x, i) => {
      const tick: RxClockFaceTick = {
        value: i ? i + 12 : 0,
        uiValue: (i ? i + 12 : 0).toString(),
        isDisabled: false
      };

      return tick;
    })
];
const HOURS_12_TICKS: RxClockFaceTicks = [
  Array(12)
    .fill(0)
    .map((x, i) => {
      const tick: RxClockFaceTick = {
        value: i || 12,
        uiValue: (i || 12).toString(),
        isDisabled: false
      };

      return tick;
    })
];
const MINUTES_TICKS: RxClockFaceTicks = [
  Array(60)
    .fill(0)
    .map((x, i) => {
      const tick: RxClockFaceTick = {
        value: i,
        uiValue: (i % 5 ? '' : i).toString(),
        isDisabled: false
      };

      return tick;
    })
];
const SECONDS_TICKS: RxClockFaceTicks = [
  Array(60)
    .fill(0)
    .map((x, i) => {
      const tick: RxClockFaceTick = {
        value: i,
        uiValue: (i % 5 ? '' : i).toString(),
        isDisabled: false
      };

      return tick;
    })
];

export function getClockTicks(face: RxClockFace): RxClockFaceTicks {
  switch (face) {
    case RxClockFace.Hours24:
      return HOURS_24_TICKS;
    case RxClockFace.Hours12:
      return HOURS_12_TICKS;
    case RxClockFace.Minutes:
      return MINUTES_TICKS;
    case RxClockFace.Seconds:
      return SECONDS_TICKS;
  }
}
